export default (sequelize, Sequelize) => {
  const Carlendar = sequelize.define("carlendar", {
    name_th: {
      type: Sequelize.STRING,
    },
    name_en: {
      type: Sequelize.STRING,
    },
    types: {
      type: Sequelize.STRING,
    },
    documentFiles: {
      type: Sequelize.STRING,
    },
    table_name: {
      type: Sequelize.STRING,
    },
    index: {
      type: Sequelize.INTEGER,
    },
  });
  return Carlendar;
};
